.main-container {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    min-height: 100vh;
    background-color: aliceblue;
}

.login-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100vh;
    width: 100%;
}

.message-container {
    /* background-color: aliceblue; */
    padding: 15px; /* Adjust the padding as needed */
    position: relative; /* Add this line */
}

.image-container {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    /* min-height: 330px; */
}

.img {
    width: 330px;
    margin: 0 auto;
    border-radius: 25px;
    margin-top: 10px;
}

.input-container {
    display: flex;
}

.input_ao_ta {
    height: 50px;
    width: 250px;
    border-radius: 50px;
    text-align: center;
}

.input_ao_btn {
    height: 50px;
    width: 50px;
    border-radius: 50px;
}

/* .help-div{
    position: fixed;
    bottom: 0;
} */

.help_btn {
    position: fixed;
    bottom: 0;
    right: 0;
    margin: 10px;
    font-size: 16px;
    width: 200px;
    height: 50px;
    border-radius: 50px;
    background-color: brown;
    color: white;
}

.spotify-container{
    margin: 10px;
}

.footer{
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content:flex-start;
    height: 70px;
    background-color: black;
}

.footer-icon{
    display: flex;
    /* align-items: center;
    justify-content: center; */
    height: 30px;
    width: 30px;
    margin: 20px 0;
    margin-left: 10px;
}